import React from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import PDF from '../data/IlyaYudkovichResume.pdf'
import ReactGA from 'react-ga';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;


export default class Resume extends React.Component {
    constructor(props) {
        super(props);

        this.header = "My Resume"
        this.text = "Please download it"
        this.resume_location = '../data/IlyaYudkovichResume.pdf'

        ReactGA.initialize('UA-149150472-1');
        console.log('initialized google');
    
        ReactGA.pageview(window.location.pathname + window.location.search);
        
        this.state = {
            open: false,
            toggleShow: this.props.toggleShow
        }
    }

    handleClick  = () => {
        this.props.toggleShow(true, this.header, this.getHtmlContent());
    }

    getHtmlContent() {
        return(
            <div className="return-value text-center">
                <a href={this.resume_location} target = "_blank" onClick={this.onResumeClick}>View my Resume!</a>
            </div>
        )
    }
    
    onResumeClick() {
        window.open(PDF);
      }
    
    render() {
        return(
            <div className="content">
                <div className="col">
                <button className="btn btn-block" type="button" onClick={this.handleClick}>
                    <strong>Resume</strong>
                </button>
                </div>
            </div>
        )}
}