import React from 'react'
import MovieReview from './MovieReview'
import movies from '../json/movies.json'
import ReactGA from 'react-ga';


export default class MovieBlog extends React.Component {
    constructor(props) {
        super(props);

        this.movies = movies.movies;
        this.placeholder_text = "Movie Suggestion"

        this.handleSubmitMovieClick.bind(this);

        ReactGA.initialize('UA-149150472-1');
        console.log('initialized google');
    
        ReactGA.pageview(window.location.pathname + window.location.search);

        this.state = {
            movie_value : ""
        }
    }

    handleSubmitMovieClick = () => {
        console.log('clicked submit button for movie suggestion')
        alert('thanks for submitting ' + this.state.movie_value)
    }

    handleMovieChange = (e) => {
        e.preventDefault();
        this.setState({
            movie_value : e.target.value
        });
    }

    render() {
        return(
            <div className="body">
                <form>
                    <div className="form-row align-items-left">
                        <div className="col-6">
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">
                                        Any Movie Suggestions:
                                    </div>
                                </div>
                                <input type="text" onChange={this.handleMovieChange} name="movie" className="form-control" id="movieSuggestions" placeholder={this.placeholder_text}/>
                            </div>
                        </div>
                        <button type="button" onClick={this.handleSubmitMovieClick} className="btn btn-info">Submit Suggestion!</button>
                    </div>
                </form>
                {this.movies.map((item, key) => (
                    <MovieReview title={item.title} 
                                 review={item.review} 
                                 rating={item.rating} 
                                 year={item.year}
                                 author={item.author}
                                 key={key}/>
                ))}
            </div>
        )
    }
}