import React from 'react';
import experience from '../json/experience.json'


export default class Experience extends React.Component {
    constructor(props) {
        super(props);

        this.header = experience.title
        this.experiences = experience.experiences

        this.state = {
            open: false,
            toggleShow: this.props.toggleShow
        }
    }

    handleClick  = () => {
        this.props.toggleShow(true, this.header, this.getHtmlContent());
    }

    getExperienceHtmlContent(experienceItem, key) {
        return(
            <div className="experience" key={key}>
                <b className="text-left">{experienceItem.company_name},</b>
                {experienceItem.role}
                <br/>
                <p>{experienceItem.description}</p>
                <br/>
            </div>
        )    
    }

    getHtmlContent() {
        return(
            <div className="return-value">
                {this.experiences.map((item, key) => (
                    this.getExperienceHtmlContent(item, key)
                ))}
                <p>Let me know if you've got any questions</p>
            </div>
        )
    }

    render() {
        return(
            <div className="content">
                <div className="col">
                <button className="btn btn-block" type="button" onClick={this.handleClick}>
                    <strong>Experience</strong>
                </button>
                </div>
            </div>
        )}
}