import React from 'react';
import about from '../json/about.json'
import { arch } from 'os';

export default class About extends React.Component {
    constructor(props) {
        super(props);

        this.header = about.title
        this.text = about.content
        this.ending = about.end
        this.state = {
            open: false,
            toggleShow: this.props.toggleShow
        }
    }

    handleClick  = () => {
        this.props.toggleShow(true, this.header, this.getHtmlContent());
    }

    getHtmlContent() {
        return(
            <div className="return-value">
                <p>{this.text}</p>
                <p>{this.ending}</p>
            </div>
        )
    }

    render() {
        return(
            <div className="content">
                <div className="col">
                <button className="btn btn-block" type="button" onClick={this.handleClick}>
                    <strong>About</strong>
                </button>
                </div>
            </div>
        )}
}