import React from 'react';
import Image from 'react-bootstrap/Image'
import cover from '../images/personal_cover.jpg'

export default class Landing extends React.Component {
    constructor(props)  {
        super(props);
        this.cover = cover;
        this.quote = "What am I even tripping for? Everything is going to work out perfectly, the way it's supposed to."
        this.author = "Tony Stark"
    }

    render() {
        return(
            <div className="row">
            <div className="col-12 col-md-8">
                <Image className="cover" src={this.cover} roundedCircle fluid/>
            </div>
            <div className="col-3 align-self-center">
                <h1 className="heading">{this.quote}</h1>
                <h5 className="subheading">-{this.author}</h5>
            </div>
        </div>
        )
    }
}