import React from 'react'

export default class MovieReview extends React.Component {
    constructor(props) {
        super(props); 
        this.title = this.props.title;
        this.review = this.props.review;
        this.rating = this.props.rating;
        this.year = this.props.year
        this.author = this.props.author;
    }

    render() {
        return(
            <div className="content panel panel-default">
                <br/>
                <div className="content panel-body">
                    <h3>{this.title} ({this.year})</h3>
                    <p>
                        {this.review}
                    </p>
                    <p>
                        <b>Rating:</b> {this.rating}
                        <br/>
                        <b>Written by:</b> {this.author}
                    </p>
                </div>
            </div>
        )
    }
}